@use "sass:math";

// インナー幅設定
// コンテンツのインナー幅（最も共通の幅が多い部分）
$inner: 1080px;
// ※キュンパス幅ではなくインナーの幅を指す

// 余白
$padding-pc: 25px;
$padding-sp: 15px;

////////////////////
// フォント設定
////////////////////

$base-font-family:"Noto Sans JP", sans-serif;
$english-font-family: "Gotu",sans-serif;
$second-font-family: "Noto Serif JP", serif;
// ※検証ツールでしっかり反映されているか確認する

body {
  font-family: $base-font-family;
}

// フォントウェイト
$regular: 400;
// $normal: 400;
$medium:500;
$bold: 700;

////////////////////
// 色の指定
////////////////////

//色指定（共通性のあるものは出来る限り指定すると良い）
$blue: #408F95;
$white: #fff;
$brown:#796A64;
$black:#0D2936;
$light-blue:#DDF0F1;

