@use "global" as *;

.campaign-card {
  // min-width: rem(280); /* カードの幅を固定 */
  display: flex;
  flex-direction: column; /* 縦に要素を積み上げる */
  flex-grow: 1; /* カードを可能な限り同じ高さに引き伸ばす */
  position: relative;
  height: 100%;
  @include mq("md") {
    // width: rem(333);
  }
}

.campaign-card__layout {
  display: flex;
  flex-direction: column;
  height: 100%;
  flex-grow: 1; /* カードを可能な限り同じ高さに引き伸ばす */
}

.campaign-card__img {
  overflow: hidden;
  z-index: 1;
  position: relative;
  object-fit: cover;
  height: auto;
  aspect-ratio: 280 / 188;
  width: 100%;
  transition: transform 0.8s;
  @include mq("md") {
    aspect-ratio: 333 / 223;
  }
}
.campaign-card__img img {
  aspect-ratio: 280 / 188;
  object-fit: cover;
  height: auto;
  width: 100%;
  transition: transform 0.8s;
}

@include mq("md") {
  .campaign-card a:hover > figure > img {
    transform: scale(1.2);
  }
}

.campaign-card__body {
  width: 100%;
  padding: rem(16) rem(16) rem(19);
  display: flex;
  flex-direction: column;
  flex: 1;
  @include mq("md") {
    padding: rem(26) rem(16) rem(28);
  }
}

.campaign-card__title-container {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
}

.campaign-card__category {
  color: $blue;
  font-size: rem(12);
  font-weight: $regular;
  font-family: $base-font-family;
  border: solid 1px $blue;
  text-align: center;
  align-content: center;
  line-height: 1;
  padding: rem(8) 0;
  min-width: rem(108);
  width: rem(108);
  @include mq("md") {
    min-width: rem(128);
    width: rem(128);
    padding: rem(10) 0;
  }
}

.campaign-card__title {
  font-size: rem(16);
  font-weight: $medium;
  font-family: $base-font-family;
  color: $black;
  margin-top: rem(4);
  padding-bottom: rem(8);
}

.campaign-card__text-wrap {
  text-align: center;
  padding-top: rem(8);
  border-top: 1px solid $blue;
  display: flex;
  flex-direction: column;
  @include mq("md") {
    padding-top: rem(16);
    padding-right: rem(20);
  }
}

.campaign-card__text {
  font-size: rem(14);
  font-family: $base-font-family;
  font-weight: $regular;
  color: $black;
  @include mq("md") {
    font-size: rem(16);
  }
}

.campaign-card__price-wrap {
  display: flex;
  align-items: flex-end;
  justify-content: space-between;
}

.campaign-card__subprice {
  color: #c94800;
  text-decoration: line-through;
  text-decoration-thickness: rem(3);
}
.campaign-card__subprice span {
  font-family: "Lato", sans-serif;
  font-size: rem(20);
  font-weight: $bold;
  color: $black;
  margin-left: rem(19);
  line-height: 2.4;
  @include mq("md") {
    font-size: rem(24);
    margin-left: rem(17);
  }
}

.campaign-card__price {
  font-family: "Lato", sans-serif;
  font-size: rem(32);
  font-weight: $bold;
  color: $blue;
  margin-right: rem(18);
  @include mq("md") {
    font-size: rem(40);
    margin-right: 0;
  }
}

//////////////
.campaign-card--big {
  box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
  @include mq("md") {
    width: 100%;
  }
}
.campaign-card__body--big {
  @include mq("md") {
    padding: rem(23) rem(25) rem(44);
  }
}
.campaign-card__title--big {
  @include mq("md") {
    font-size: rem(24);
    margin-top: rem(6);
    padding-bottom: 0;
  }
}
.campaign-card__text-wrap--big {
  @include mq("md") {
    display: block;
    padding-top: rem(26);
    padding-right: rem(0);
  }
}
.campaign-card__text-wrapper--big {
  display: none;
  @include mq("md") {
    display: block;
    margin-top: rem(22);
  }
}
.campaign-card__price-wrap--big {
  @include mq("md") {
    justify-content: center;
    gap: rem(40);
  }
}
.campaign-card__subprice--big span {
  @include mq("md") {
    margin-left: 0;
  }
}
.campaign-card__text-info {
  display: none;
  @include mq("md") {
    font-weight: $regular;
    display: block;
    text-align: initial;
    margin-top: rem(-13);
  }
}

.campaign-card__contact-wrap {
  text-align: center;
}
.campaign-card__day {
  font-family: "Lato", sans-serif;
  font-size: rem(16);
  font-weight: $bold;
  color: $blue;
  margin-top: rem(38);
  letter-spacing: 0.4px;
  @include mq("md") {
    margin-top: rem(30);
  }
}
.campaign-card__contact-info {
  font-family: $base-font-family;
  font-size: rem(14);
  font-weight: $regular;
  color: $blue;
  margin-top: rem(1);
}
.campaign-card__button {
  display: none;
  @include mq("md") {
    display: block;
    margin-top: rem(16);
  }
}
///////////////

.campaign-card--aside.campaign-card--aside {
  width: 100%;
  box-shadow: 2px 2px 10px 0px rgba(0, 0, 0, 0.25);
}

.campaign-card__img--aside {
  overflow: hidden;
  z-index: 1;
  position: relative;
  object-fit: cover;
  height: auto;
  aspect-ratio: 294 / 188;
  width: 100%;
  transition: transform 0.8s;
  @include mq("md") {
    aspect-ratio: 294 / 188;
  }
}
.campaign-card__img--aside img {
  aspect-ratio: 294 / 188;
  object-fit: cover;
  height: auto;
  width: 100%;
  transition: transform 0.8s;
}
.campaign-card__body--aside.campaign-card__body--aside {
  padding: rem(14) rem(16) rem(10);
}
.campaign-card__title--aside {
  text-align: center;
}
.campaign-card__text-wrap--aside.campaign-card__text-wrap--aside {
  padding-top: 0;
  padding-right: 0;
}
.campaign-card__text--aside.campaign-card__text--aside {
  font-family: $base-font-family;
  font-size: rem(14);
  font-weight: $regular;
  color: $black;
  margin-top: rem(10);
}
.campaign-card__price-wrap--aside.campaign-card__price-wrap--aside {
  align-items: initial;
}
.campaign-card__price--aside.campaign-card__price--aside {
  font-size: rem(32);
  margin-right: rem(25);
}
